/* jshint ignore:start */


define('~fastboot/app-factory', ['peepchat/app', 'peepchat/config/environment'], function(App, config) {
  App = App['default'];
  config = config['default'];

  return {
    'default': function() {
      return App.create(config.APP);
    }
  };
});


/* jshint ignore:end */
